<template>
  <div class="container">
    <AuthorTutorial></AuthorTutorial>
    <div class="left-column">
      <div class="mindmap-container">
        <div v-html="mindMap" @click="handleWordClick"></div>
      </div>
    </div>
    <div class="right-column" v-if="showWriteArea">
      <div class="author">
        <div style="display: flex; align-items: center;">
          <label>Story Name </label>
          &emsp;
          <input v-model="inputTitle">
          <span style="flex-grow: 1;"></span>
          <button class="button" @click="importFile()">Import Story </button>
        </div>
        <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
        <div class="para-container">
          <p class="preserve-line-breaks" @click="handleWordClick">{{ para }}</p>
        </div>
        <div>
          <div>
            <textarea v-model="mindText" v-if="developer"></textarea>
            <button v-on:click="generateMindMap" v-if="developer">generate MindMap</button>
          </div>
          <textarea class="responsive-textarea" v-model="inputText" placeholder="write story here"></textarea>
          <br /><br />
          <label>From para index (behind sentence): </label>
          <input v-model="paraIndexFrom" />
          <br /><br />
          <label>From the para word (click) "{{ wordIndex }}".</label>
          <input v-model="wordIndex" />
          <br /><br />
          <label>What para index go to: </label>
          <input v-model="paraIndexGoTo" />

          <br /><br />
          <!-- @=v-on -->
          <button @click="addPara">Add Para</button>
          &emsp;&emsp;&emsp;&emsp;&emsp;&emsp;
          <button @click="downloadPara">Download Story Locally</button>
        </div>
      </div>
    </div>
    <button class="write-hidden-btn" v-show="!showWriteArea" @click="toggleWriteArea">
      <img src="../assets/header/back-arrow-4845.png" alt="Enpand" class="icon-toggle">
    </button>
    <button class="write-hidden-btn" v-show="showWriteArea" @click="toggleWriteArea">
      <img src="../assets/header/arrow-203.png" alt="Indent" class="icon-toggle">
    </button>
  </div>
</template>

<script >
import { saveAs } from 'file-saver'
import mermaid from 'mermaid';
import { useNovelStore } from '../stores/novelStore'
import { useStore } from '../stores/store'
import AuthorTutorial from '../components/tutorials/AuthorTutorial.vue';
export default {
  components: {
    AuthorTutorial,
  },
  setup() {
    const store = useStore();
    const novelStore = useNovelStore();
    return { store, novelStore }
  },
  data() {
    return {
      developer: false,
      step: 1,
      inputTitle: 'None',
      wordIndex: ' ',
      paraIndexFrom: 0,
      paraIndexGoTo: 1,
      goBack: false,
      para: ' ',
      inputText: '',
      allStory: '',
      paragraphs: [],
      index: 0,
      paraList: ["Story Begin"],
      paraMindList: ["Story Begin [0]"],
      text: '',
      graph: '',
      mindText: '0["Story Begin"]',
      mindMap: '',
      alpha: '',
      novel: {
        title: '',
        author: '',
        content: ''
      },
      showWriteArea: true
    }
  },
  methods: {
    toggleWriteArea() {
      this.showWriteArea = !this.showWriteArea;
    },
    importFile() {
      document.getElementById('file-input').click();
    },
    onFileInputChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = (event) => {
        const text = event.target.result;
        this.parseNovelText(text);
      };
      reader.readAsText(file);
    },
    parseNovelText(text) {
      const lines = text.trim().split('\n');
      let title = '';
      let author = '';
      let content = '';
      for (let line of lines) {
        if (line.startsWith('Story Title: ')) {
          title = line.slice(13);
        } else if (line.startsWith('Authors: ')) {
          author = line.slice(9);
        } else {
          content += line + '\n';
        }
      }
      this.novel.title = title;
      this.inputTitle = title;
      this.novel.author = author;
      this.novel.content = content;
      const nodes = content.split('\n');

      nodes.forEach(paragraphText => {
        const matches = paragraphText.match(/\[(\d+)\] \[(.*?)\] (.+) \[(\d+)\]/);
        if (matches) {
          const sourceIndex = parseInt(matches[1]);
          const wordIndex = matches[2];
          const content = matches[3].replaceAll('"', "'");
          const paragraphIndex = parseInt(matches[4]);

          this.paraIndexFrom = sourceIndex;
          this.wordIndex = wordIndex;
          this.inputText = content;
          this.paraIndexGoTo = paragraphIndex;
          this.addPara(false);
        }
      });
      this.generateMindMap();
    },

    addPara(updateMindMap = true) {
      if (this.paraIndexFrom > this.index || this.paraIndexGoTo > this.index + 1) {
        alert(",Go to");
      } else {

        //index
        //In fact, only this.paraIndexGoTo = this.index + 1 is true
        if (this.inputText.includes('"')) {
          alert("");
          this.inputText = this.inputText.replaceAll('"', "'");
        }
        this.paragraphs.push({
          sourceIndex: this.paraIndexFrom,
          wordIndex: this.wordIndex,
          content: this.inputText,
          paragraphIndex: this.paraIndexGoTo
        });

        if (this.paraIndexGoTo > this.index) {
          this.index += 1;
        }
        //
        if (this.paraIndexFrom < this.index) {
          this.mindText += '\n' + this.paraIndexFrom + '["' + this.paraMindList[this.paraIndexFrom] + '"]';
        }
        //mermaid
        //
        this.mindText += this.wordIndex === ' ' ? '--->' : '-->|' + this.wordIndex + '|';
        //
        this.goBack = this.paraIndexGoTo === this.index ? false : true
        this.alpha = this.inputText;
        const textWithLineBreaks = this.splitText(this.alpha, 10);
        this.paraList.push(this.inputText);
        this.paraMindList.push(textWithLineBreaks + ' [' + this.index + ']');
        this.inputText = '[' + this.paraIndexFrom + '] ' + '[' + this.wordIndex + '] ' + this.inputText;

        if (this.goBack) {
          this.mindText += this.paraIndexGoTo + '["' + this.paraMindList[this.paraIndexGoTo] + '"]';
        } else {
          this.mindText += this.index + '["' + textWithLineBreaks + '[' + this.index + ']"]';
        }

        this.para += this.inputText + ' [' + this.paraIndexGoTo + ']' + '\n';
        this.allStory += this.inputText + ' [' + this.index + ']' + '\n';
        this.inputText = '';
        this.wordIndex = ' ';
        this.paraIndexFrom = this.paraIndexGoTo;
        this.paraIndexGoTo = this.index + 1;
        if (updateMindMap) {
          this.generateMindMap();
        }

      }

    },
    downloadPara() {
      var blob = new Blob(["Story Title: " + this.inputTitle + '\n' + this.allStory], {
        type: "text/plain;charset=utf-8"
      });
      saveAs(blob, this.inputTitle + ".txt");
    },

    handleWordClick() {
      const selection = window.getSelection();
      const range = selection.getRangeAt(0);
      const text = range.toString().trim();

      if (text !== '') {
        // 
        const targetTextNode = range.endContainer;
        //
        const textAfterSelection = targetTextNode.textContent.slice(range.endOffset);
        const nextParaIndexMatch = textAfterSelection.match(/\[(\d+)\]/);

        if (nextParaIndexMatch) {
          const paraIndex = parseInt(nextParaIndexMatch[1], 10);
          this.paraIndexFrom = paraIndex;
        } else {
          //,2
          const whichParagraph = this.paragraphs.find((paragraph) => {
            const contentAlpha = "[" + paragraph.sourceIndex + "] " + "[" + paragraph.wordIndex + "] " + paragraph.content + " [" + paragraph.paragraphIndex + "]"
            return contentAlpha.toLowerCase().includes(targetTextNode.textContent.toLowerCase());
          });
          this.paraIndexFrom = whichParagraph.paragraphIndex;
        }


        this.wordIndex = text;
      }
    },

    async generateMindMap() {
      const mermaidText = `graph TB\n${this.mindText}`;
      // MindMap
      mermaid.initialize({
        flowchart: {
          htmlLabels: false,
          curve: 'linear'
        },
        theme: 'default',
        fontFamily: '"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif',
        fontSize: 14,
        startOnLoad: true,
        securityLevel: 'loose',
        logLevel: 3,
        useMaxWidth: false,
        allowParallelEdges: false,
        marginRight: 50,
        arrowMarkerAbsolute: false,
        nodeSpacing: 50,
        flowstate: {
          active: { fill: 'rgba(130,130,130,0.5)', "font-size": "12px" },
          inactive: { fill: 'rgba(130,130,130,0.5)', "font-size": "12px" },
        },
        useWidth: false,
      });

      const result = await mermaid.mermaidAPI.render('mindMapSvg', mermaidText);
      const svg = result.svg;
      this.mindMap = svg;

      //  SVG 
      this.$nextTick(() => {
        const svgElement = document.getElementById('mindMapSvg');
        this.attachZoomAndDragHandlers(svgElement);
      });
    },

    attachZoomAndDragHandlers(svgElement) {
      let scale = 1;
      let translateX = 0;
      let translateY = 0;
      let mouseDown = false;
      let prevMousePos;
      let altKeyPressed = false;

      function updateTransform() {
        svgElement.style.transform = `translate(${translateX}px, ${translateY}px) scale(${scale})`;
      }

      svgElement.addEventListener('wheel', (e) => {
        e.preventDefault();
        if (e.shiftKey) {
          // shift
          translateX += Math.sign(e.deltaY) * 10;
        } else {
          // 
          translateY += Math.sign(e.deltaY) * 10;
        }
        updateTransform();
      });

      svgElement.addEventListener('wheel', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        const delta = Math.sign(e.deltaY) * -0.1;
        scale += delta;
        scale = Math.max(0.1, Math.min(3, scale));
        updateTransform();
      });

      svgElement.addEventListener('mousedown', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = true;
        prevMousePos = {
          x: e.clientX,
          y: e.clientY
        };
      });

      svgElement.addEventListener('mousemove', (e) => {
        if (!mouseDown || !altKeyPressed) return;
        e.preventDefault();
        const dx = e.clientX - prevMousePos.x;
        const dy = e.clientY - prevMousePos.y;
        translateX += dx;
        translateY += dy;
        updateTransform();
        prevMousePos = {
          x: e.clientX,
          y: e.clientY
        };
      });

      svgElement.addEventListener('mouseup', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = false;
      });

      svgElement.addEventListener('mouseleave', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = false;
      });

      // 
      window.addEventListener('keydown', (e) => {
        if (e.key === 'Alt') {
          altKeyPressed = true;
        }
      });

      window.addEventListener('keyup', (e) => {
        if (e.key === 'Alt') {
          altKeyPressed = false;
        }
      });
    },
    splitText(text, wordsPerLine) {
      const words = text.split(' ');
      let result = '';
      for (let i = 0; i < words.length; i++) {
        result += words[i] + (i % wordsPerLine === wordsPerLine - 1 ? '\n' : ' ');
      }
      return result;
    },


  }
};
</script>

